.info{
  display: flex;
  position: relative;
  &-picture{
    img{
      width: 100%;
      height: 100%;
    }
    @include respond-to("max_mobile") {
      display: none;
		}
    @include respond-to("max_tablet") {
      display: none;
		}
    @include respond-to("tablet") {
      display: none;
		}
    @include respond-to("desktop") {
      display: none;
		}
  }
  &-about{
  
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &-min__title{
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: .25em;
    color: #a3adb7;
    font-weight: 500;
    display: inline-block;
    margin-bottom: 34px;
    @include respond-to("max_mobile") {
			margin-bottom: 10px;
      margin-top: 10px;
      padding-left: 5px;
      padding-right: 5px;
		}
    @include respond-to("max_tablet") {
			margin-bottom: 10px;
      margin-top: 30px;
      padding-left: 5px;
      padding-right: 5px;
		}
    @include respond-to("tablet") {
			padding-inline: 10px;
		}
    @include respond-to("desktop") {
			padding-inline: 20px;
		}
    
    &::before{
      content: "";
      display: none;
      margin-right: 12px;
      border-width: 5px;
      border-style: solid;
      border-top-color: transparent;
      border-left-color: transparent;
      display: inline-block;
      border-right-color: #ff8f26;
      border-bottom-color: #ff8f26;
    }
  }
  &-title{
    margin-bottom: 34px;
    @include respond-to("max_mobile") {
			padding-inline: 5px;
		}
    @include respond-to("max_tablet") {
			padding-inline: 5px;
		}
    @include respond-to("tablet") {
			padding-inline: 10px;
		}
    @include respond-to("desktop") {
			padding-inline: 20px;
		}
    h2{
      font-size: 50px;
      letter-spacing: -1px;
      line-height: 1;
      font-weight: 600;
      color: black;
      
    }
  }
  &-details{
    margin-bottom: 50px;
    color: #768492;
    font-family: hind,Sans-serif;
    font-weight: 400;
    @include respond-to("max_mobile") {
			width: 90%;
      padding-left: 5px;
      padding-right: 5px;
		}
    @include respond-to("tablet") {
			width: 90%;
      padding-left: 10px;
      padding-right: 10px;
		}
    @include respond-to("desktop") {
			width: 90%;
      padding-left: 10px;
      padding-right: 10px;
		}
  }
  &-sayac{
    display: flex;
    @include respond-to("max_mobile") {
			display: flex;
      flex-wrap: wrap;
      justify-content: center;
		}
    @include respond-to("max_tablet") {
			padding-inline: 30px;

		}
    @include respond-to("tablet") {
			display: flex;
      justify-content: center;
      margin-bottom: 30px;

		}
    @include respond-to("desktop") {
			display: flex;
      justify-content: center;
      margin-bottom: 30px;

		}
    &__box{
      text-align: left;
      margin-right: 50px;
      @include respond-to("max_mobile") {
        flex: 0 0 50%;
        max-width: 50%;
        margin-right: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
      }
      .number{
        color: #ff8f26;
        font-size: 50px;
        font-weight: 600;
        letter-spacing: -.25px;
      }
      .title{
        color: #000;
        font-size: 20px;
        text-transform: none;
        line-height: 40px;
        font-weight: 600;
        letter-spacing: -.25px;
        @include respond-to("max_tablet") {
          line-height: 1.3;
          margin-bottom: 20px;
          text-align: center;
        }
        @include respond-to("desktop") {
          line-height: 1.3;
          margin-bottom: 20px;
          text-align: center;
        }
      }
    }
  }
}