.buttons-container {
  &.ui-pos-center {
    text-align: center;
    width: 100%;
  }
}

@mixin button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  @include padding-h($gutter);
  font-family: inherit;
  text-align: center;
  line-height: 1.2;
  outline: 0;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -0.5px;
  border-radius: $border-radius-lg;
  height: 50px;
  color: #fff;
  position: relative;
  transition: .3s;

  .icon {
    font-size: 20px;
    color: #fff;
    transition: .3s;

    &:not(.ui-no-pos-right) {
      margin-left: 15px;
    }

    &.ui-no-pos-right {
      margin-right: 12px;
    }

    &:not([class*="ui-size-"]) {
      font-size: 20px;
    }

    &.ui-size-small {
      font-size: 18px;
    }
  }

  &.ui-width-min {
    min-width: 250px;
  }

  &.ui-width-full {
    width: 100%;
  }

  &:hover {
    .icon {
      &:not(.ui-no-hover-translate-x) {
        transform: scale(1.1) translateX(3px);
      }

      &.ui-no-hover-translate-x {
        transform: scale(1.1);
      }
    }
  }

}

// -------------------------------------
//   Button
// -------------------------------------
.button {
  @include button;

  &:not([class*="ui-color-"]) {
    background: $color-text;
  }

  &.ui-color-wp {
    background: #25d366;
  }
  &.ui-color-dark {
    color: black !important;
  }

  &.ui-button-style-1 {
    border-radius: 0px;
		z-index: 1;
    color: #fff;
    background-color: transparent;
    &:hover {
      &::before {
        clip-path: polygon(0 0,calc(100% + 40px) 0,100% 100%,0 100%);
        width: 100%;
        height: 100%;
      }
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: darkorange;
      clip-path: polygon(0 0,40px 0,0 100%,0 100%);
      z-index: -1;
      transition: .6s cubic-bezier(0.165, 0.84, 0.44, 1);
    }
    i{
      margin-left: 20px;
    }
    @include respond-to("max_mobile") {
			position: relative;
      bottom: 30px;
		}
  }
  &.ui-button-style-2 {
    border-radius: 0px;
		z-index: 1;
    color: #fff;
    margin-top: 70px;
    background-color: transparent;
    &:hover {
      &::before {
        clip-path: polygon(0 0,calc(100% + 40px) 0,100% 100%,0 100%);
        width: 100%;
        height: 100%;
      }
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: darkorange;
      clip-path: polygon(0 0,40px 0,0 100%,0 100%);
      z-index: -1;
      transition: .6s cubic-bezier(0.165, 0.84, 0.44, 1);
    }
    i{
      margin-left: 20px;
    }
  }

  &.ui-color-1 {
    border-radius: 0px;
		z-index: 1;
    color: #fff !important;
    background-color: darkorange;
    &:hover {
    }

    &::before {
  
    }
    i{
      margin-left: 20px;
      color: white !important;
    }
  }

  &.ui-color-2 {
    background: $color-secondary;
  }

  &.ui-color-3 {
    border-radius: 0px;
		z-index: 1;
    color: #ff8f26 !important;
    transition: .4s;
    &:hover {
      background-color: #ff8f26;
      color: #000 !important;
      i{
        color: #000 !important;
      }
    }

    &::before {
  
    }
    i{
      margin-left: 20px;
      color: #ff8f26 !important;
    }
  }

}

.button-border {
  @include button;

  &:not([class*="ui-color-"]) {
    color: $color-text;
    border: 1px solid rgba($color-text, .15);

    .icon {
      color: $color-text;
    }
  }

  &.ui-color-light {
    color: #fff;
    border: 1px solid #fff;

    .icon {
      color: #fff;
    }
  }

  &.ui-color-1 {
    color: $color-primary;
    border: 1px solid rgba($color-primary, .2);

    &:hover {
      background-color: rgba($color-primary, .1);
    }

    .icon {
      color: $color-primary;
    }
  }

  &.ui-color-2 {
    color: $color-secondary;
    border: 1px solid rgba($color-secondary, .2);

    .icon {
      color: $color-secondary;
    }
  }

}

// -------------------------------------
//   Button Icon
// -------------------------------------
.button-icon {
  display: inline-flex;
  position: relative;
  align-items: center;
  font-family: inherit;
  justify-content: space-between;
  padding-right: 5px;
  box-shadow: 0 5px 20px rgba($color-primary, .15);

  &.ui-width-full {
    width: 100%;
  }

  &:not([class*="ui-size-"]) {
    height: 52px;
    padding-left: $gutter;
    border-radius: $border-radius-lg;

    .button-icon__text {
      font-size: 16px;
      letter-spacing: -0.5px;
    }

    .icon {
      @include size(42px);
      border-radius: $border-radius-lg;
      font-size: 20px;
      margin-left: 25px;
    }
  }

  &.ui-size-medium {
    height: 40px;
    padding-left: 12px;
    border-radius: $border-radius-lg;

    .button-icon__text {
      font-size: 13px;
      letter-spacing: -0.5px;
    }

    .icon {
      @include size(32px);
      border-radius: $border-radius-md;
      font-size: 18px;
      margin-left: $gutter;
    }
  }

  &:not([class*="ui-color-"]) {
    .button-icon__text {
      color: $color-text;
    }
    .icon {
      background-color: $color-text;
      box-shadow: 0 5px 15px 0 rgba($color-text, .2);  
    }
    &:hover {
      .icon {
        background-color: lighten($color-text, 10);
      }
    }
  }
  
  &.ui-color-1 {
    .button-icon__text {
      color: $color-primary;
    }
    .icon {
      background-color: $color-primary;
      box-shadow: 0 5px 15px 0 rgba($color-primary, .2);  
    }
    &:hover {
      .icon {
        background-color: lighten($color-primary, 10);
      }
    }
  }

  &.ui-color-2 {
    .button-icon__text {
      color: $color-secondary;
    }
    .icon {
      background-color: $color-secondary;
      box-shadow: 0 5px 15px 0 rgba($color-secondary, .2);  
    }
    &:hover {
      .icon {
        background-color: lighten($color-secondary, 10);
      }
    }
  }

  &.ui-color-continues {
    .button-icon__text {
      color: $color-secondary;
    }
    .icon {
      background-color: $color-secondary;
      box-shadow: 0 5px 15px 0 rgba($color-secondary, .2);  
    }
    &:hover {
      .icon {
        background-color: lighten($color-secondary, 10);
      }
    }
  }

  &__text {
    font-weight: 700;
    text-align: left;
    flex: 1;
    line-height: 1.3;

    .heading {
      display: block;
      font-size: 11px;
      color: $color-text-light;
      font-weight: 500;
    }
  }

  .icon {
    color: #fff;
    align-items: center;
    justify-content: center;
    transition: .3s;
  }
}

// -------------------------------------
//   Button Line
// -------------------------------------
.button-line {
	display: inline-flex;
	align-items: center;
  text-transform: uppercase;
  font-weight: 700;
  position: relative;
  letter-spacing: -0.5px;
  transition: .3s;

  &:hover {
    .icon {
      transform: scale(1.1) translateX(3px);
    }
  }
    
  .icon {
    transition: .3s;
	}

  &:not([class*="ui-size-"]) {
    font-size: 14px;

    .icon {
      margin-left: 15px;
      font-size: 20px;  
    }
  }

  &.ui-size-small {
    font-size: 12px;

    .icon {
      font-size: 18px;
      margin-left: 12px;
    }
  }

  &:not([class*="ui-color-"]) {
    color: $color-text;
    .icon {
      color: $color-text;
    }
  }

  &.ui-color-1 {
    color: $color-primary;
    .icon {
      color: $color-primary;
    }
  }

  &.ui-color-2 {
    color: $color-secondary;
    .icon {
      color: $color-secondary;
    }
  }

}