.sidebar {

  &-nav {
    background: #fff;
    clip-path: polygon(8% 0, 100% 0, 100% 60%, 100% 100%, 0 100%, 0% 60%, 0 5%);
    @include respond-to("max_mobile") {
      margin-top: $gutter;
    }
    ul {
      li {
        .categories-baslik{
          height: 100px;
          padding-left: 20px;
          padding-top: 25px;
          font-weight: 600;
          font-size: 40px;
          &:first-child {
          border-bottom: 1px solid rgba($color-text, .1);
          margin-right: 30px;
          margin-left: 30px;
          width: 80%;
          }
        }
        a{
          margin-left: 30px;
          &::before{
            content: "";
            width: 5px;
            height: 5px;
            display: inline-block;
            vertical-align: middle;
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%);
            transition: all .3s ease;
            background-color: #ff8f26;
          }
        }
        &:first-child {
          a {
            border-radius: $border-radius-md $border-radius-md 0 0;
          }
        }
        &:last-child {
          a {
            border-radius: 0 0 $border-radius-md $border-radius-md;
          }
        }
        // &:not(:last-child) {
        //   border-bottom: 1px solid rgba($color-text, .1);
        // }

        &:hover, &.is-current {
          a {
            color: #ff8f26;
            .icon {
              transform: translateX(0);
              opacity: 1;
              visibility: visible;
            }
          }
        }

        a {
          color: $color-text-light;
          font-size: 14px;
          font-weight: 700;
          display: flex;
          align-items: center;
          position: relative;
          padding: 17px 25px;
          transition: .3s;

          .icon {
            position: absolute;
            right: 25px;
            color: $color-text;
            opacity: 0;
            visibility: hidden;
            transform: translateX(-15px);
            transition: .3s;
            will-change: transform;
            font-size: 16px;
          }

        }
      }
    }
  }
  &-nav__posts{
    margin-top: 50px;
    width: 100%;
    min-height: 500px;
    margin-bottom: 100px;
    background-color: white;
    clip-path: polygon(8% 0, 100% 0, 100% 60%, 100% 100%, 0 100%, 0% 60%, 0 5%);
    ul {
      li {
        .posts-baslik{
          height: 100px;
          padding-left: 20px;
          padding-top: 30px;
          font-weight: 600;
          font-size: 30px;
          &:first-child {
          border-bottom: 1px solid rgba($color-text, .1);
          margin-right: 30px;
          margin-left: 30px;
          width: 80%;
          margin-bottom: 20px;
          }
        }
        a{
          margin-left: 10px;
          margin-right: 30px;
          .post-box{
            display: flex;
            width: 100%;
            &__image{
              width: 80px;
              height: 80px;
              img{
                width: 100%;
                height: 100%;
                object-fit: contain;
              }
            }
            &__title{
              padding-left: 15px;
              display: flex;
              flex: 1;
              flex-direction: column;
              .title{
                padding-top: 15px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                color: black;
                font-size: 16px;
                line-height: 1;
                font-weight: 600;
                transition: .2s;
                &:hover{
                  color:  #ff8f26;
                }
              }
              .date{
                display: block;
                font-size: 11px !important;
                font-size: .55556rem;
                color: #afb5bb;
                letter-spacing: .2em;
                padding-top: 5px;
              }
            }
          }
        }
        &:first-child {
          a {
            border-radius: $border-radius-md $border-radius-md 0 0;
          }
        }
        &:last-child {
          a {
            border-radius: 0 0 $border-radius-md $border-radius-md;
          }
        }
        // &:not(:last-child) {
        //   border-bottom: 1px solid rgba($color-text, .1);
        // }

        &:hover, &.is-current {
          a {
            color: #ff8f26;
            .icon {
              transform: translateX(0);
              opacity: 1;
              visibility: visible;
            }
          }
        }

        a {
          color: $color-text-light;
          font-size: 14px;
          font-weight: 700;
          display: flex;
          align-items: center;
          position: relative;
          padding: 17px 25px;
          transition: .3s;

          .icon {
            position: absolute;
            right: 25px;
            color: $color-text;
            opacity: 0;
            visibility: hidden;
            transform: translateX(-15px);
            transition: .3s;
            will-change: transform;
            font-size: 16px;
          }

        }
      }
    }
  }

}