.default{
  .owl-stage-outer {
    padding: 30px;
    margin: -30px;
  }
  &-baslik{
    padding: 80px 70px 50px 80px;
    
  }

  &-card {
    overflow: hidden;
    transition: .4s;
    &:hover{
      box-shadow: 0 10px 15px -8px rgba(0,0,0,.3);
      transform: translateY(-10px);
      .default-body {
        background-color: $color-primary;
      }
      .default-body{
        .default-message{
          transition: .4s;
          color: white;
        }
        .default-profil{
          transition: .4s;
          color: white;
          .profil-name{
            transition: .4s;
            color: black;
          }
          .profil-level{
            transition: .4s;
            color: white;
          }
        }
      }
    }
  }

  &-body{
    background-color: white;
    padding: 50px;
    font-family: 'Roboto Condensed', sans-serif;
    clip-path: polygon(6% 0, 100% 0, 100% 100%, 0 100%, 0 4%);
    transition: .4s;
    @include respond-to("max_mobile") {
			height: 550px;
		}
    @include respond-to("max_tablet") {
      height: 550px;
      padding: 30px;
    }
  }
  &-message{
    color: #596877;
    font-size: 20px;
    line-height: 36px;
    letter-spacing: -.25px;
    margin: 0 0 60px;
    font-weight: 500;
    @include respond-to("max_tablet") {
      font-size: 26px;
      font-weight: 600;
      margin-top: 60px;
    }
  }
  &-profil{
    display: flex;
    align-items: center;
    flex-direction: column;
    font-weight: 700;
    .profil-img{
      width: 80px;
      height: 80px;
      line-height: 0;
      img{
        border-radius: 50%;
        margin-bottom: 20px;
      }
    }
    .profil-name{
      font-size: 20px;
      line-height: 24px;
      letter-spacing: -.25px;
      margin-top: 15px;
      margin-bottom: 1px;
    }
    .profil-level{
      color: #afb5bb;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -.25px;
    }
  }
}