.page-content {

  // -------------------------------------
  //   Page content header
  // -------------------------------------
  &__header {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;

    @include respond-to("max_desktop") {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }

    .title {
      color: $color-primary;
      font-size: 20px;
      letter-spacing: -0.8px;
      font-weight: 800;
      line-height: 1.3;
    }
    .date {
      display: inline-flex;
      align-items: center;
      color: $color-text;
      font-size: 14px;
      font-weight: 600;
      margin-left: 50px;
      flex: 0 0 auto;
      width: auto;
      max-width: 100%;
      @include respond-to("max_desktop") {
        margin-top: 10px;
        margin-left: 0;
      }
      .icon {
        margin-right: 5px;
        font-size: 13px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: rgba($color-text, .5);
        @include size(30px);
        align-items: center;
        font-size: 18px;
      }
    }
  }

  // -------------------------------------
  //   Page content heading
  // -------------------------------------
  &__heading {
    color: $color-dark;
    font-weight: 800;
    font-size: 14px;
    margin-bottom: 10px;

    &.ui-color-1 {
      color: $color-primary;
    }
    &.ui-color-2 {
      color: $color-primary;
    }
  }

  // -------------------------------------
  //   Page content photo
  // -------------------------------------
  &__photo {
    overflow: hidden;
    height: 300px;
    float: left;
    margin-right: 30px;
    margin-bottom: 5px;
    width: 50%;
    position: relative;

    //
    @include respond-to("tablet") {
      margin-left: 0;
      width: 100%;
      float: none;
    }
    @include respond-to("max_tablet") {
      height: auto !important;
      margin-right: 0;
      float: none;
      width: 100%;
      margin-bottom: 30px;
    }
    //
    &.ui-small, &.ui-large {
      width: 100%;
      float: inherit;
      margin-right: 0;
    }
    &.ui-small {
      height: 175px;
      margin-bottom: 0;
    }
    &.ui-large {
      height: 400px;
      @include respond-to("tablet") {
        height: 250px;
      }
    }
    &.ui-auto {
      height: auto;
    }
    img {
      @include size(100%);
      overflow: hidden;
      object-fit: cover;
    }
  }

  // -------------------------------------
  //   Page content spot
  // -------------------------------------
  &__spot {
    margin-bottom: $gutter;
    font-size: 16px;
    color: $color-dark-2;
    font-weight: 600;
    letter-spacing: -0.5px;
  }

  // -------------------------------------
  //   Page content description
  // -------------------------------------
  &__description {
    color: $color-text-light !important;
    font-size: 14px;
    line-height: 1.7;
    font-family: $font-primary !important;

    &.ui-txt-center {
      text-align: center;
    }
    
    a {
      color: #55acef;
      font-weight: 600;
      &:hover {
        text-decoration: underline;
      }
    }

    h3 {
      font-weight: 700;
      font-size: 24px;
      letter-spacing: -1px;
    }
    h5 {
      font-weight: 700;
      font-size: 14px;
    }

    > p {
      margin-bottom: 30px;
    }

    a {
      color: #55acef;
      font-weight: 600;
      &:hover {
        text-decoration: underline;
      }
    }

    strong {
      font-weight: 800;
    }
    img {
      max-width: 100%;
    }
    ul, ol {
      padding-left: $gutter;
    }
    ul li {
      list-style: disc;
    }
    ol li { 
      list-style: decimal;
    }
    table {
      width: 100% !important;
      border: 1px solid rgba(black,.08) !important;
      tbody {
        tr {
          border-bottom: 1px solid rgba(black,.08) !important;
          td, th {
            padding: 10px $gutter !important;
            border-right: 1px solid rgba(black,.08) !important;
            line-height: 1.5;
            &:last-child {
              border-right: 0 none !important;
            }
          }
        }
      }
    }
  }

  // -------------------------------------
  //   Page content gallery
  // -------------------------------------
  &__gallery {
    ul {
      li {
        .gallery-photo {
          width: 100%;
          height: 140px;
          border-radius: $border-radius-lg;
          overflow: hidden;
          &:hover {
            img {
              transform: scale(1.1);
            }
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: .8s;
          }
        }
        @include respond-to("max_mobile") {
          flex: 0 0 50%;
          max-width: 50%;
        }
      }
    }
  }

  // -------------------------------------
  //   Page content cards
  // -------------------------------------
  &__cards {
    margin-top: 70px;
    margin-bottom: 30px;

    &.ui-contact {
      margin-top: 0;
      margin-bottom: 0;
      .card {
        height: 385px;

        .text {
          height: 70px;
        }
      }
    }

    ul {
      li {
        @include respond-to("max_tablet") {
          margin-bottom: 30px;
        }
      }
    }

    .card {
      height: 410px;
      border-radius: $border-radius-md;
      box-shadow: 0 5px 35px rgba(black, .1);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      @include padding-h(50px);
      padding-bottom: 50px;

      @include respond-to("max_mobile") {
        @include padding-h(30px);
      }
      @include respond-to("mobile_l") {
        height: 250px !important;
      }
      @include respond-to("tablet") {
        @include padding-h(25px);
        height: 350px;

      }
      @include respond-to("desktop") {
        @include padding-h(30px);

      }

      &:hover {
        > .icon {
          transform: translateY(-10px);
          &::after {
            transform: translateY(10px) scale(.75);
          }
        }
      }

      .title {
        color: $color-dark;
        font-size: 20px;
        font-weight: 700;
        line-height: 1.4;
        @include respond-to("mobile_l") {
          font-size: 16px;
        }
        @include respond-to("tablet") {
          font-size: 16px;;
        }
      }
      > .icon {
        color: $color-primary;
        font-size: 72px;
        margin-top: 25px;
        margin-bottom: 50px;
        position: relative;
        transition: transform 0.35s,;
        transition-timing-function: cubic-bezier(.12,.845,.305,1);
        @include respond-to("mobile_l") {
          font-size: 54px;
          margin-bottom: 25px;
        }
        @include respond-to("tablet") {
          font-size: 54px;
        }
        &::after {
          position: absolute;
          height: 8px;
          width: 66px;
          content: "";
          bottom: -25px;
          left: 50%;
          margin-left: -33px;
          background: rgba(black, .06);
          z-index: -1;
          border-radius: 100%;
          transform: scale(1);
          transition: transform .35s;
          transition-timing-function: cubic-bezier(.12,.845,.305,1);
        }
      }
      .text {
        font-size: 16px;
        font-weight: 500;
        color: $color-text;
        line-height: 1.4;
        @include respond-to("mobile_l") {
          display: none;
        }
        @include respond-to("tablet") {
          font-size: 13px;
        }
      }
      .btn-wrapper {
        position: absolute;
        @include pos-bottom(0, 40px, 0);
        @include respond-to("mobile_l") {
          bottom: 25px;
        }
        a {
          color: $color-primary;
          font-size: 16px;
          font-weight: 600;
          display: inline-flex;
          align-items: center;
          position: relative;
          z-index: 1;

          @include respond-to("mobile_l") {
            font-size: 12px;
          }
          @include respond-to("tablet") {
            font-size: 13px;
          }

          &::after {
            content: "";
            position: absolute;
            @include pos-bottom(-10px, 0, -10px);
            background: $color-primary;
            z-index: -1;
            height: 0;
            transition: height .3s;
          }

          &:hover {
            color: #fff;
            &::after {
              height: 100%;
            }
          }

          .icon {
            font-size: 14px;
            margin-left: 10px;

            @include respond-to("mobile_l") {
              font-size: 12px;
            }
            @include respond-to("tablet") {
              font-size: 13px;
            }
          }
        }
      }
    }
  }

  // -------------------------------------
  //   Page content information
  // -------------------------------------
  &__information {
    margin-top: 70px;

    ul {
      li {
        &:not(:last-child) {
          border-bottom: 1px solid rgba($color-text-light, .15);
          margin-bottom: 50px;
          padding-bottom: 50px;
        }
      }
    }
    .heading {
      .icon {
        color: rgba($color-text-light, .3);
        font-size: 60px;

        .fa-secondary {
          opacity: .5;
        }
      }
      .desc {
        padding-left: $gutter;
        line-height: 1.2;
        .small-title {
          color: $color-primary;
          font-size: 16px;
          font-weight: 700;
        }
        .title {
          color: $color-primary;
          font-size: 36px;
          letter-spacing: -2px;
          font-weight: 700;
        }
      }
    }

    .content {
      @include respond-to("min_laptop") {
        padding-left: 50px;
      }
      @include respond-to("max_laptop") {
        padding-top: 50px;
      }
    }
  }

} 