.MzYydGFzYXLEsW12ZXlhesSxbMSxbWFqYW5zxLE {
  box-sizing: inherit;
}

.popup {
  position: fixed;
  @include pos-fixed;
  background: rgba($color-primary, .9);
  z-index: 2000;
  display: flex;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  &-wrapper {
    @include size(100%);
    max-height: 100%;
    padding: 55px;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    transform: translateY(100px);
    @include respond-to("max_tablet"){
      padding: 0;
    }
    &.is-visible {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
    .cover {
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      @include size(100%);
      a {
        max-width: 90%;
        text-align: center;
        padding: 3px;
        background: #fff;
        box-shadow: 0 0 30px rgba(black, .15);
      }
      img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
      }
    }
  }

  &-close {
    position: absolute;
    @include pos-top-right(30px);
    @include size(1em);
    font-size: 28px;
    z-index: 12;
    display: flex;
    justify-content: center;
    align-items: center;
    &:before, &:after {
      content: "";
      position: absolute;
      background: #fff;
      height: 2px;
      width: 100%;
    }
    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
  }
}