.MzYydGFzYXLEsW12ZXlhesSxbMSxbWFqYW5zxLE {
  box-sizing: inherit;
}
.footer{
  width: 100%;
  height: 100%;
  background-color: #2a2c38;
  &-list{
    padding-top: 35px;
    padding-bottom: 35px;
    @include respond-to("max_tablet") {
      display: flex;
      justify-content: center;
      text-align: center;
    }
  }
  ul{
    li{
      a{
        color: #768492;
        transition: .4s;
        &:hover{
          color: #ff8f26 !important;
        }
      }     
    }
  }
  &-logo{
    svg{
      width: 215px;
      height: 27px;
    }
    @include respond-to("max_tablet") {
      display: flex;
      justify-content: center;
    }
  }
  &-bottom{
    background-color: #1f212c;
    height: 70px;
    .text{
      display: flex;
      align-items: center;
      height: 100%;
      font-size: 20px;
    }
  }
  .social-box{
    display: flex;
    width: 100%;
    height: 50px;
    justify-content: space-around;
    @include respond-to("max_mobile") {
      margin-top: 20px;
    }
    @include respond-to("max_tablet") {
      margin-top: 40px;
    }
    .icon{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px;
      background-color: #32343e;
      transition: .4s;
      &:hover{
        background-color: #ff8f26;
      }
      i{
        color: white;
      }
    }
  }
}