.news{
  margin-top: 80px;
  margin-bottom: 80px;
  @include respond-to("max_mobile") {
    margin-top: 0px;
  }
  &-box{
    padding: 70px 0px 70px 0px !important;
    @include respond-to("max_mobile") {
      padding: 40px 0px 35px 0px !important;
    }
    @include respond-to("max_tablet") {
      padding: 10px 0px 0px 0px !important;
    }
    @include respond-to("tablet") {
      padding: 150px 0px 0px 40px !important;
      position: sticky;
      top: 20px;
    }
    @include respond-to("desktop") {
      padding: 150px 0px 0px 40px !important;
      position: sticky;
      top: 20px;
    }
    .title{
      margin-top: 20px;
      line-height: 1 !important;
      @include respond-to("tablet"){
        font-size: 30px !important;
        margin-top: 10px !important;
      }
    }
    .button{
      @include respond-to("max_mobile") {
        margin-top: 20px;
        margin-bottom: -80px;
      }
      @include respond-to("max_tablet") {
        margin-top: 30px;
        margin-bottom: -10px;
      }
      @include respond-to("tablet") {
        margin-top: 20px;
        margin-bottom: -10px;
      }
    }
  }
  &-bulten{
    padding-top: 70px;
  }
  &-title__date{
    display: flex;
    padding-top: 10px;
    font-size: 14px;
    .title{
      color: darkorange;
      font-weight: 400;
      &::after{
        content: "";
        display: inline-block;
        width: 4px;
        height: 4px;
        background-color: #d2d2d2;
        vertical-align: middle;
        margin: 5px;
      
      }
    }
    .date{
      color: #afb5bb;
    }
  }
  &-title{
    padding-top: 10px;
    h4{
      font-weight: 600;
      font-size: 26px;
      line-height: 1.3;
      @include respond-to("max_mobile") {
        font-size: 22px;
      }
      @include respond-to("max_tablet") {
        font-size: 24px;
      }
    }
  }

  &-picture{
    width: 100%;
    height: 209px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: .6s;
      &:hover{
        transform: scale(1.1);
      }
    }
  }
}