// -------------------------------------
//   Scroll Controller
// -------------------------------------
.ui-no-scroll { overflow: hidden; }
.ui-no-scroll-y { overflow-y: hidden; }
.ui-no-scroll-x { overflow-x: hidden; }

// -------------------------------------
//   Position Default
// -------------------------------------
.ui-pos-relative {
  position: relative;
}
.ui-pos-inherit {
  position: inherit;
}
.ui-pos-sticky {
  position: sticky;
  top: $gutter;
}

// -------------------------------------
//   Position Alignment
// -------------------------------------
.ui-text-center {
  text-align: center;
}
.ui-justify-center {
  justify-content: center;
}
.ui-justify-end {
  justify-content: flex-end;
}
.ui-justify-start {
  justify-content: flex-start;
}
.ui-justify-between {
  justify-content: space-between;
}
.ui-align-center {
  align-items: center;
}
.ui-align-end {
  align-items: flex-end;
}
.ui-align-start {
  align-items: flex-start;
}
.ui-align-between {
  align-items: space-between;
}

.ui-z-index {
  position: relative;
  z-index: 9;
}

// -------------------------------------
//   Hidden
// -------------------------------------
.ui-hidden {
  display: none;
}
@include respond-to("max_mobile") {
  .ui-xs-hidden { display: none }
}
@include respond-to("max_tablet") {
  .ui-sm-hidden { display: none }
}
@include respond-to("max_desktop") {
  .ui-md-hidden { display: none }
}

$values: 5px, 10px, 15px, 20px, 25px, 30px, 40px, 50px, 60px, 70px, 80px, 90px, 100px;

// -------------------------------------
//   Padding
// -------------------------------------
@each $value in $values {
  .ui-pv-#{$value} {
    @include padding-v($value);
  }
}

.ui-pv-0 {
  @include padding-v(0);
}

.ui-pt-0 { padding-top: 0 !important; }
.ui-pb-0 { padding-bottom: 0 !important; }

// -------------------------------------
//   Padding Top
// -------------------------------------
@each $value in $values {
  .ui-pt-#{$value} {
    padding-top: $value;
  }
}

// -------------------------------------
//   Padding Bottom
// -------------------------------------
@each $value in $values {
  .ui-pb-#{$value} {
    padding-bottom: $value;
  }
}

// -------------------------------------
//   Margin
// -------------------------------------
@each $value in $values {
  .ui-mv-#{$value} {
    @include margin-v($value);
  }
}

.ui-mv-0 {
  @include margin-v(0);
}

.ui-mt-0 { margin-top: 0 !important; }
.ui-mb-0 { margin-bottom: 0 !important; }
.ui-ml-0 { margin-left: 0 !important; }
.ui-mr-0 { margin-right: 0 !important; }

// -------------------------------------
//   Margin Top
// -------------------------------------
@each $value in $values {
  .ui-mt-#{$value} {
    margin-top: $value;
  }
}

// -------------------------------------
//   Margin Bottom
// -------------------------------------
@each $value in $values {
  .ui-mb-#{$value} {
    margin-bottom: $value;
  }
}
