.header{
  width: 100%;
  height: 170px;
  position: relative;
  z-index: 1112;
  background-color: white;
  &-top{
    height: 50px;
    background-color: #2a2c38;
  }
  &-social{
    height: 50px;
    display: flex;
    .header-social__icon{
      display: flex;
      align-items: center;
      padding-right: 15px;
      .icon{
        color: darkorange;
        padding-right: 5px
      }
      a{
        font-weight: 600;
        color: white;
        font-size: 14px;
        transition: .3s;
        &:hover{
          color: darkorange;
        }
      }
    }
  }
  &-logo{
    svg{
      width: 215px;
      height: 27px;
    }
  }
  &-bottom{
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .home-menu{
      height: 50px;
      display: flex;
      align-items: center;
      .off-box{
        width: 150px;
        display: flex;
        z-index: 1;
        position: absolute;
        background-color: #2a2c38;
        flex-direction: column;
        a{
          padding-left: 40px;
          padding-right: 40px;
          padding-top: 8px;
          padding-bottom: 8px;
          color: white;
          text-align: center;
          position: relative;
          &:hover{
            background-color: gray;
            
            }
          }
        }
      }
      ul{
        li{
          a{
            color: black;
            font-weight: 600;
            text-decoration: none;
            padding-right: 10px;
            padding-left: 5px;
          }
          i{
            font-size: 10px;
            padding-left: 2px;
          }
          .button{
            color: black;
            i{
              color: black;
            }
          }
        }
      }
      .button {
        color: white;
      }
    }
  }
