.scrolltop {
	display: inline-flex;
	justify-content: center;
	align-items: center;
  position: fixed;
  @include pos-bottom-right(35px, 70px);
	background: $color-primary;
  @include size(60px);
	border-radius: 50%;
  text-align: center;
  color: #fff;
  z-index: 2002;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.5);
  transition: .3s;
  
	@include respond-to("max_tablet") {
    @include size(50px);
    @include pos-bottom-right(15px, 70px);
  }
  
  .icon {
		font-size: 20px;
		@include respond-to("max_tablet") {
			font-size: 16px;
		}
  }
  
  &.is-visible {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}