label.checkbox {
  display: inline-flex;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 7px;
  }

  &.is-error {
    .checkbox {
      border: 2px solid red !important;
    }
    .text {
      color: red !important;
      
      a {
        color: red !important;
      }
    }
  }

  input {
    position: absolute;
    left: -9999px;
  }

  .checkbox {
    display: inline-flex;
    @include size(20px);
    background: none;
    border: 2px solid rgba($color-text, .08);
    border-radius: $border-radius;
    transition: .2s;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      top: -5px;
      line-height: 0.7;
      font-size: 24px;
      transform: scale(0);
      color: $color-primary;
      opacity: 0;
      visibility: hidden;
      transition: .2s;
    }
  }

  .text {
    color: $color-text;
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding-left: 10px;
    span {
      color: rgba($color-text-light, .7);
      margin-left: 5px;
    }
    a {
      font-weight: 700;
      color: $color-primary;
    }
  }
  
  input:checked + .checkbox:before {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}