.features{
  background-color: white;
  height: 100%;
  
  &-body{
    padding-top: 80px;
    padding-bottom: 40px;
    padding-left: 40px;

    @include respond-to("max_mobile") {
			width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding-top: 40px;
      padding-left: 25px;
      padding-right: 25px;
      padding-bottom: 0px;
		}
    @include respond-to("max_tablet") {
			width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      text-align: center;
      padding-top: 70px;
      padding-left: 25px;
      padding-right: 25px;
      padding-bottom: 40px;
		}
  }
  &-box{
    background-color: #ff8f26;
    height: 100%;
    display: flex;
    align-items: flex-start;
    text-align: left;
    justify-content: center;
    flex-direction: column;
    padding-left: 85px;
    padding-top: 30px;
    padding-right: 10px;
    @include respond-to("max_mobile") {
      
      height: 380px;
      padding-inline: 50px;
      padding-block: 50px;
		}
    @include respond-to("max_tablet") {
      height: 380px;
      padding-inline: 60px;
      padding-block: 50px;
    }

    .number {
      font-size: 180px;
      font-weight: 700;
      line-height: 0.5;
      letter-spacing: -8px;
      color: #fff;
      @include respond-to("max_mobile") {
        font-size: 140px;
      }
      @include respond-to("max_tablet") {
        font-size: 150px;
      }

    }
    .year {
      font-size: 18px;
      font-weight: 600;
      color: #fff;
      letter-spacing: -0.5px;
      line-height: 1;
      display: flex;
      justify-content: flex-end;
      max-width: 220px;
      margin-top: 5px;
      margin-bottom: 10px;
      @include respond-to("max_mobile") {
        max-width: 167px;
        line-height: 1.4px;
      }
      @include respond-to("max_tablet") {
        max-width: 180px;
        line-height: 0.6;
      }
    }
    .title {
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -1px;
      color: #fff;
      line-height: 1.3;
      @include respond-to("max_mobile") {
        font-size: 30px;
        padding-top: 10px;
      }
      @include respond-to("max_tablet") {
        font-size: 35px;
        padding-top: 10px;
      }
    }
  }
  &-option{
    transition: background .3s,border .3s,border-radius .3s,box-shadow .3s;
    padding: 40px 70px 0px 80px;
    @include respond-to("max_mobile") {
      padding: 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding-left: 20px;
		}
    @include respond-to("max_tablet") {
      padding: 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      padding-left: 20px;
		}

    .option-body{
      @include respond-to("max_mobile") {
        display: flex;
        flex-direction: column;
        text-align: center;
      }
      @include respond-to("max_tablet") {
        display: flex;
        flex-direction: column;
        text-align: center;
      }
      .text{
        margin-top: 20px;
        margin-bottom: 40px;
        font-family: "Hind", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
        font-weight: 400;
        color: #768492;
        margin-right: 27px;
        @include respond-to("max_mobile") {
          margin-bottom: 10px;
        }
        @include respond-to("max_tablet") {
          margin-bottom: 10px;
        }
      }
      .icon{
        position: relative;
        @include respond-to("max_mobile") {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 10px;
          margin-top: 20px;
        }
        @include respond-to("max_tablet") {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-bottom: 10px;
          margin-top: 20px;
        }
        i{
          width: 1em;
          height: 1em;
          position: relative;
          display: block;
          color: #ff8f26;
          position: absolute;
          left: -40px;
          font-size: 23px;
          font-weight: 400;
          @include respond-to("max_mobile") {
            position: relative;
            left: 0px;
            margin-bottom: 15px;
          }
          @include respond-to("max_tablet") {
            position: relative;
            left: 0px;
            margin-bottom: 15px;
          }
        }
        h4{
          font-size: 27px;
          font-weight: 700;
          color: black;
          transition: .4s;
          &:hover{
            color: #ff8f26;
          }

        }
      }
    }
  }
  &-play{
    top: 44%;
    left: 39%;
    position: absolute;
    .icon{
      top: 5%;
      i{
        color: #ff8f26;
        font-size: 80px;
      }
    }
  }
  &-picture{
    height: 380px;
    width: 100%;
    position: relative;
    
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-box__two{
    background-color: #2a2c38;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    letter-spacing: -1px;
    @include respond-to("max_mobile") {
			height: 230px;
		}
    @include respond-to("max_tablet"){

      height: 250px;
    }
    h2{
      text-align: center;
      font-size: 30px;
      line-height: 40px;
      color: white;
      max-width: 100%;
      font-family: 'Roboto Condensed', sans-serif;
      font-style: normal;
      width: 65%;
    }
    .icon{
      text-transform: capitalize;
      font-weight: 700;
      letter-spacing: 0;
      margin-top: 20px;
      color: #ff8f26;
      i{
        color: #ff8f26;
        margin-left: 10px;
      }
    }
  }
}