.change{
  
  width: 100%;
  background-color: white;
  &-image{
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-body{
    padding-top: 40px;
    padding-bottom: 40px;
    @include respond-to("max_mobile") {
      padding-top: 20px;
      padding-left: 20px;
      padding-bottom: 85px;
      padding-right: 0px;
      text-align: left;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;
    }
    @include respond-to("tablet") {
      padding-top: 40px;
      padding-left: 40px;
      padding-bottom: 100px;
      padding-right: 0px;
      text-align: left;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;
    }
    @include respond-to("max_tablet") {
      padding-top: 20px;
      padding-left: 20px;
      padding-bottom: 85px;
      padding-right: 0px;
      text-align: left;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;
    }
    @include respond-to("desktop") {
      padding-top: 20px;
      padding-left: 20px;
      padding-bottom: 85px;
      padding-right: 0px;
      text-align: left;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;
    }
    .section-header{
      .title{
        @include respond-to("max_mobile") {
          margin-top: 5px;
          margin-bottom: 10px;
        }
        @include respond-to("max_tablet") {
          line-height: 1;
          margin-top: 10px;
          margin-bottom: 10px;
          font-size: 45px;
        }
      }
      .text{
        @include respond-to("max_mobile") {
          text-align: start;
        }
      }
    }
  }

  .accordion {
    ul {
      li {
        .item {
          display: flex;
          align-items: flex-start;
          padding-top: 30px;
          cursor: pointer;
          .icon {
            font-size: 18px;
            color: $color-primary;
            width: 30px;
            height: 30px;
            border-radius: 999px;
            border: 2px solid rgba($color-primary, 1);
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 15px;
          }
          
        }

        &.is-current {
          .accordion-dropdown {
            height: 85px;
            .text {
              transform: translateY(0);
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }

    &-dropdown {
      flex: 1;
      height: 30px;
      transition: .4s;
      .title, .text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      .title {
        font-size: 20px;
        font-weight: 700;
        letter-spacing: -0.5px;
        line-height: 1.3;
        margin-bottom: 5px;
        -webkit-line-clamp: 1;
      }
      .text {
        font-size: 16px;
        color: rgba($color-text-light, .7);
        -webkit-line-clamp: 2;
        transform: translateY(-10px);
        opacity: 0;
        visibility: hidden;
        transition: .4s;
      }
    }
  }
}