body {
	font-family: $font-primary;
	font-size: 16px;
	line-height: 1.6;
	font-weight: 400;
	color: $color-text;
	letter-spacing: -0.2px;
	overflow-x: hidden;
  background-color: #f5f5f5;
	
	&.ui-bg {
		background: $bg;
	}

	&.is-scroll {
		overflow: hidden;
	}

  .main {
    min-height: 5000px;
  }

  .parallax-mirror {
    background-color: $color-dark;
    img {
      opacity: .7;
    }
  }

}

.small-title {
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: .25em;
    color: #a3adb7;
    font-weight: 500;
    display: inline-block;
    margin-bottom: 10px;
    margin-top: -10px;

    

    &::before{
      content: "";
      display: none;
      margin-right: 12px;
      border-width: 5px;
      border-style: solid;
      border-top-color: transparent;
      border-left-color: transparent;
      display: inline-block;
      border-right-color: #ff8f26;
      border-bottom-color: #ff8f26;
    }
}

.section-header {
  &.ui-txt-center {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    .prlx-title__min{
      font-size: 14px;
      font-weight: 600;
      letter-spacing: 2px;
      margin-bottom: 10px;
      @include respond-to("max_mobile") {
        font-size: 10px;
      }
    }
    .prlx-title{
      font-size: 80px;
      letter-spacing: -1px;
      font-weight: 600;
      line-height: 1;
      @include respond-to("max_mobile") {
        font-size: 30px;
      }
      @include respond-to("max_tablet") {
        font-size: 40px;
      }
      @include respond-to("tablet") {
        font-size: 50px;
      }
      @include respond-to("desktop") {
        font-size: 50px;
      }
    }
  }

  &.ui-txt-light {
    .title, .text {
      color: #fff;
    }
  }

  &:not([class*="ui-txt-size-"]) {
    .title {
      font-size: 54px;
      font-weight: 600;
      letter-spacing: 1px;
      @include respond-to("max_mobile") {
        font-size: 32px;
      }  
    }
  }

  &.ui-txt-size-large {
    .title {
      font-size: 70px;
      font-weight: 700;
      letter-spacing: -1px;
    }
  }
  
  &.ui-txt-size-small {
    .title {
      font-size: 54px;
      font-weight: 500;
      letter-spacing: -1px;
    }
  }

  &.ui-txt-size-title {
    font-size: 54px;
    font-weight: 500;
    letter-spacing: -1px;
    color: #ff8f26;
  }

  .title {
    color: $color-dark-2;
    line-height: 1.3;
    font-size: 54px;
    font-weight: 500;
    letter-spacing: -1px;
    span {
      color: $color-primary;
    }
    @include respond-to("max_tablet"){
      line-height: 1.1;
      font-size: 48px;
    }
  }

  .text {
    color: #768492;
    font-family: hind,Sans-serif;
    font-weight: 400;
    margin-top: 2px;
    margin-right: 30px;
    @include respond-to("max_mobile") {
			margin-right: 0px;
      text-align: center;
      padding-bottom: 40px;
		}
  }
  
}