.project{
  @include respond-to("max_mobile") {
    margin-top: 46px;
    margin-bottom: 37px;
  }
  &-menu{
    .section-header{
      text-align: center;
      width: 100%;
    }
  }
  &-gallery{
    margin-top: 50px;
    
    .gallery-box{
      width: 100%;
      height: 280px;
      position: relative;
      overflow: hidden;
      &:hover{
        background-color: black;
        .gallery-box__text{
          transform: translateY(0);
        }
        img{
          opacity: 0.6;
        }
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 1;
      }
      &__text{
        height: 100%;
        position: absolute;
        z-index: 10;
        top: 0;
        display: flex;
        flex-direction: column;
        padding-top: 130px;
        padding-left: 50px;
        transition: .6s;
        transform: translateY(150px);
        &:hover{
          .min-text{
            text-decoration: underline 2px solid;
          }
          .title{
            text-decoration: underline 2px solid;
          }
        }
      }
      .min-text{
        color: #ff8f26;
        font-weight: 600;
      }
      .title{
        color: white;
        font-size: 20px;
        font-weight: 600;
      }
    }
    .project-button{
      width: 100%;
      text-align: center;
    }
  }
}