.road{
  margin-top: 50px;
  margin-bottom: 50px;
  &-card{
    position: relative;
    &:hover {
      .road-body {
        transform: translateY(0);
        .text {
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
  &-photo{
   
    img{
      clip-path: polygon(6% 0, 100% 0, 100% 100%, 0 100%, 0% 12%);

      filter: brightness(60%) invert(0);
    };
  }
  &-body{
    position: absolute;
    z-index: 99;
    color: white;
    top: 60%;
    left: 10%;
    transform: translateY(20px);
    transition: .4s;
    .title{
      font-size: 26px;
      font-weight: 600
    }
    .text{
      color: darkorange;
      font-weight: 600;
      opacity: 0;
      visibility: hidden;
      transition: .3s;
      i{
        margin-left: 10px;
      }
    }
  }
}