.navigation {
  display: flex;
  align-items: center;

  .button{
    color: black;
    font-size: 16px;
    font-weight: 600;
    .icon{
      font-size: 14px;
    }
    i{
      color: black;
    }
  }

  @include respond-to("max_desktop") {
    display: none
  }

  @include margin-h( -1 * $nav-gutter );
  @include respond-to("min_large") {
    @include margin-h( -1 * $nav-gutter-large );
  }

  @include respond-to("desktop") {
    @include margin-h( -1 * $nav-gutter-small );
  }

  &-item {
    $triangle-size: 11px;

    
    @include padding-h( $nav-gutter );
    @include respond-to("min_large") {
      @include padding-h( $nav-gutter-large );
    }
    
    @include respond-to("desktop") {
      @include padding-h( $nav-gutter-small );
    }

    &.is-triangle {
      > a {
        &::after {
          display: block;
        }
      }
    }

    //
    
    > a {
      display: inline-flex;
      align-items: center;
      color: $color-dark;
      font-size: 20px;
      font-weight: 700;
      height: $header-height-bottom;
      font-family: inherit;
      line-height: 1.2;
      position: relative;
      z-index: 1;
      transition: .3s;
      @include respond-to("desktop") {
        font-size: 14px;
      }

      .icon {
        font-size: 10px;
        padding-left: 3px;
        padding-top: 1px;
      }
      
      &::before {
        content: "";
        position: absolute;
        @include pos-bottom(0, 35px, 0);
        height: 2px;
        margin: 0 auto;
        background: #2a2c38;
        width: 0;
        transition: width .3s;
      }
      &::after {
        content: "";
        position: absolute;
        @include pos-bottom;
        @include size(0);
        z-index: 2;
        margin: 0 auto;
        @include triangle-top(
          $triangle-size, 
          #fff
        );
        opacity: 0;
        visibility: hidden;
        display: none;
      }
    }

    // Hover
    &:hover {


      > .navigation-links {
        opacity: 1;
        visibility: visible;
        li{
          a{
            color: white;
          }
        }
      }

      .navigation-dropdown {
        opacity: 1;
        visibility: visible;
      }

      .navigation-dropdown__content {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        transition: .3s;
      }

    }
  }

  &-links {
    position: absolute;
    background: #2a2c38;
    box-shadow: 0 20px 40px rgba($color-text, .1);
    min-width: 230px;
    @include padding-v(10px);
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    
    &::before {
      content: "";
      position: absolute;
      left: 30px;
      top: -10px;
      border-bottom: 10px solid #000;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
    }
    //
    > li {
      display: block;
      padding-left: 30px;
      width: 100%;
      &:hover {
        background: gray;
        transition: .9s;
        
        a{
          color: #afb5bb;
        }
      }
      > a {
        display: block;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: -0.5px;
        padding: 12px 30px;
        color: white;
        transition: .3s;
      }
    }
  }

  &-dropdown {
    position: absolute;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 1;
    min-height: 450px;
    opacity: 0;
    visibility: hidden;

    &::before {
      content: "";
      position: absolute;
      @include pos-fixed;
      z-index: -1;
      background: rgba($bg, .7);
    }

    &__content {
      @include padding-v(50px);
      opacity: 0;
      visibility: hidden;
      transform: translateY(20px);
    }
  }

}