.slide {
	position: relative;
}

// -------------------------------------
//   Slide height
// -------------------------------------
.slide, .slide-material, .hero {
	height: $slide-height;

	@include respond-to("mobile_l") {
		height: 550px;
	}
	@include respond-to("max_mobile") {
	 	height: $slide-height-mobile;
	}

}

// -------------------------------------
//   Slide content
// -------------------------------------
.slide {
	background: black;

  &-list {
    position: relative;
  	@include pos-top-left(0, 0);
  	li {
			display: none;
  	}
	}
	
  &-material {
    overflow: hidden;
		position: relative;
  	// img {
		// 	transition: transform 10s ease-in 0s;
		// }
		img, video {
  		@include size(100%);
			object-fit: cover;
  	}
	}

	&-photo {
		> div {
			background-repeat: no-repeat;
			background-position: center center;
			background-size: cover;
			height: 100%;
			opacity: .85;
			// transition: transform 10s ease-in 0s;
		}
	}

  &-wrapper {
    position: absolute;
		@include pos-fixed;  	
	}
	
	&-container {
		height: 100%;
    display: flex;
		align-items: center;
		justify-content: flex-start;
		text-align: left;
		position: relative;
		@include respond-to("tablet") {

			display: flex;
			justify-content: center;

		}
		@include respond-to("desktop") {

			display: flex;
			justify-content: center;
		}
	}

  &-content {
		width: 80%;
		position: relative;
		z-index: 2;
		
		@include respond-to("max_tablet") {
			width: 100%;
		}
		@include respond-to("max_desktop") {
			text-align: center;
		}
		@include respond-to("tablet") {
			width: 70%;
		}
		@include respond-to("desktop") {
			width: 80%;
			margin-left: 200px;
		}
		
		@include respond-to("laptop") {
			width: 80%;
			margin-left: 300px;
		}
		.heading {
			color: #fff;
			font-size: 100px;
			font-weight: 800;
			letter-spacing: -1.5px;
      line-height: 1.2;
			@include respond-to("max_mobile") {

				font-size: 32px;

			}
			@include respond-to("max_tablet") {

				font-size: 32px;

			}
			@include respond-to("tablet") {

				font-size: 32px;

			}
			@include respond-to("desktop") {

				font-size: 40px;

			}
			@include respond-to("laptop") {

				font-size: 40px;

			}

		}

		.text {
			color: #fff;
			font-size: 20px;
			font-weight: 400;
			margin: 0 auto;
			width: 100%;
		}
		.button{
			margin-top: 50px;
			color: black;
			@include respond-to("desktop") {
				margin-left: 220px;
			}
			@include respond-to("laptop") {
				margin-left: 220px;
			}
		}
	}


	&-dots {
		$dots-color: #fff;
		$dots-size: 12px;
		//
  	position: absolute;
  	@include pos-bottom(0, 30px, 0);
  	margin: 0 auto;
  	max-width: $container;
  	width: 100%;
  	z-index: 2;
		text-align: center;
		display: none;
		@include respond-to("max_tablet") {
			display: block;
		}
		@include respond-to("max_tablet") {
			display: block;
			$dots-size: 10px;
		}
		@include respond-to("tablet") {
			display: block;
			$dots-size: 10px;
		}

  	li {
  		display: inline-block;
  		a {
				@include size($dots-size);
  			cursor: pointer;
  			display: inline-block;
				font-size: 0;
				position: relative;
				margin: 0 5px;
				border-radius: 20px;
				background: none;
				border: 2px solid #fff;
				transition: .3s;
			}
			
  		&.is-active {
				a {
					background: #fff;
					width: 100px;
				}
			}
			
  	}
  }
  &-arrows {
		position: absolute;
		@include pos-top(50%, 0, 0);
		display: block;
		width: 100%;
    margin: -30px auto 0;

		@include respond-to("max_tablet") {
			display: none;
		}
		@include respond-to("tablet") {
			display: none;
		}
  	button {
			position: absolute;
			z-index: 2;
			border-radius: 999px;
			font-size: 42px;
			color: #fff;
			transition: .3s;

			svg {
				path {
					stroke-width: 1px !important;
				}
			}

			@include respond-to("max_tablet") {
				position: relative;
				@include margin-h(5px);
				left: inherit !important;
				right: inherit !important;
			}
			@include respond-to("max_mobile") {
				@include size(45px);
				font-size: 16px;
			}

			&.prev {
				left: $gutter;
				padding-right: 2px;
			}

			&.next {
				right: $gutter;
				padding-left: 2px;
			}
			
			
  	}
	}
	
}

// -------------------------------------
//   Slide effect controller
// -------------------------------------
.slide {

	&-list {
  	li {
			&.is-effect {
				// .slide-material img, 
				// .slide-photo > div {
				// 	transform: scale(1.15);
				// }
				
				.slide-content {
					.heading, .text, .small-title, .button {
						transform: translateY(0);
						opacity: 1;
						visibility: visible;	
					}
				}
			}
  	}
	}

	&-content {
		.heading, .text, .small-title, .button {
			opacity: 0;
			visibility: hidden;
		}

		.small-title {
			transform: translateY(-40px);
			transition: .9s .1s;
		}
		.heading {
			transform: translateY(-70px);
			transition: .9s .2s;
		}
		.text {
			transform: translateY(-100px);
			transition: .9s .3s;
		}
		.button  {
			transform: translateY(-100px);
			transition: .9s .3s;
		}
	}

}