.inner-page {

  // -------------------------------------
  //   Inner page header
  // -------------------------------------
  &__header {
    position: relative;
    background-color: transparent !important;
    

  }
  
  &__wrapper{
    position: relative;
    top: -80%;
    margin-bottom: 0px !important;
  }

  &__content{
    .leader{
      height: 780px;
      width: 100%;
      clip-path: polygon(5% 0, 70% 0%, 100% 0, 100% 70%, 100% 100%, 30% 100%, 0 100%, 0 5%);

      .leader-left__box{
        position: relative;
        width: 100%;
        height: 780px;
        background-color: white;
        clip-path: polygon(5% 0, 70% 0%, 100% 0, 100% 70%, 100% 100%, 30% 100%, 0 100%, 0 5%);
        .bg-image{
          width: 100%;
          height: 780px;
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .box-text{
          position: absolute;
          top: 0;
          left: 0;
          padding: 100px;
          .body{
            .section-header{
              .small-title{

              }
              .title{
                line-height: 1;
              }
              .text{
                margin-top: 30px;
              }
              .style-box{
                margin-top: 100px;
                .name{
                  display: flex;
                  
                  flex-direction: column;

                  text-align: left;
                  .person{
                    font-weight: 600;
                    font-size: 20px;
                    font-family: 'Montserrat', sans-serif;
                  }
                }
              }
            }
          }
        }
      }
      .leader-right__box{
        // &:hover{
        //   .box-bg{
        //     background-color: darkorange;
        //     width: 100%;
        //     height: 780px;
        //     .box-image{
        //       height: 780px;
        //       width:  98%;
        //       img{
        //         width: 98%;
        //         height: 780px;
        //         object-fit: cover;
        //       }
        //     }
        //   }
        // }
        .box-bg{
          background-color: darkorange;
          width: 100%;
          height: 780px;
          transition: .6s;
          .box-image{
            height: 780px;
            width: 100%;
            transition: .4s;
            img{
              width: 100%;
              height: 780px;
              object-fit: cover;
            }
          }
        }
      }

      &-information{
        padding-top: 80px;
        .information-header{
          .small-title{
            
          }
          .title{
            margin-top: 15px;
            line-height: 1;
          }
          .text{
            padding-top: 50px;
          }
        }
        .information-values{
          padding-top: 80px;
          height: 500px;
          .values-box{
            height: 500px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            padding-inline: 40px;
            text-align: center;
            .icon{
              color: #ff8f26;
              font-size: 70px;
            }
            .title{
              padding-top: 30px;
              font-size: 24px;
              color: #fff;
              font-weight: 600;
              
            }
            .text{
              padding-top: 10px;
              color: #586776;
              font-weight: 600;
            }
            .link{
              padding-top: 30px;
            }
          }
          .solution{
            width: 100%;
            display: flex;
            justify-content: center;
            padding-top: 20px;
            a{  
              color: #ff8f26;
              padding-left: 5px;
            }
          }
          &__one{
            background-color: #ff8f26;
          }
          &__two{
            background-color: #2a2c38;
          }
          &__tree{
            background-color: white;
            .values-box{
              .title{
                color: black !important;
              }
              .text{
                color: #586776 !important; 
              }
            }
          }
        }
      }
    }
  }

  // -------------------------------------
  //   Inner page photo
  // -------------------------------------
  &__photo {
    height: 400px;
    background: black;

    @include respond-to("max_mobile") {
      height: 250px
    }

    &.ui-large {
      height: 550px;

      @include respond-to("max_tablet") {
        height: 375px
      }
    }

    img {
      @include size(100%);
      object-fit: cover;
      opacity: .65;
    }
  }

  // -------------------------------------
  //   Inner page heading
  // -------------------------------------
  &__heading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;

    &.ui-relative {
      position: relative;
      background: $overlay-bg;
      height: 330px;
    }

    .container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding-top: $header-height + $gutter;

      @include respond-to("max_tablet") {
        padding-top: $header-height-mobile;
      }
    }

    .title {
      color: #fff;
      font-size: 36px;
      font-weight: 700;
      letter-spacing: -1.5px;
      max-width: 500px;
      line-height: 1.3;
      margin-bottom: 5px;
      @include respond-to("max_mobile") {
        font-size: 32px;
      }
    }

    .text {
      color: #fff;
      font-size: 16px;
      line-height: 1.6;
      max-width: 575px;
      text-shadow: 0 0 20px rgba(black, .7);
      margin: 5px auto 0;

      @include respond-to("max_mobile") {
        font-size: 14px;
      }
      @include respond-to("mobile_l") {
        font-size: 16px;
      }
    }

  }

  // -------------------------------------
  //   Inner page nav
  // -------------------------------------
  &__nav {
    $nav-gutter-h: 15px;
    //
    border-bottom: 1px solid rgba(black, .08);
    background: #fff;

    &.ui-pos-sticky {
      z-index: 50;
      top: 0;
    }

    ul {
      justify-content: center;

      @include respond-to("max_tablet") {
        justify-content: flex-start;
        overflow-y: auto;
      }

      @include respond-to("mobile_xl") {
        justify-content: center;
      }
      
      li {
        @include padding-h($nav-gutter-h);
        flex: 0 0 auto;
        width: auto;
        max-width: 100%;

        &.heading {
          a {
            font-weight: 800;
            color: $color-dark;
          }
        }

        &:not(.heading):hover, 
        &:not(.heading).is-current {
          a {
            color: $color-text;
            &::after {
              width: 100%;
            }
          }
        }

        a {
          height: 70px;
          display: inline-flex;
          align-items: center;
          color: rgba($color-text, .6);
          font-size: 20px;
          font-weight: 600;
          position: relative;
          transition: .3s;
          &::after {
            content: "";
            position: absolute;
            @include pos-bottom;
            height: 3px;
            background: $color-primary;
            width: 0;
            transition: width .3s;
          }
        }
      }
    }
  }

}
