.parallax{
  width: 100%;
  height: 400px;
  position: relative;
  padding: 80px 0px 50px 0px;
  .image{
    height: 400px;
    width: 100%;
    background-color: black;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
      opacity: 0.5;
      @include respond-to("max_mobile") {
        opacity: 0.9;
      }
    }
  }
  &-body{
    position: absolute;
    display: flex;
    top: 20%;
    color: white;
    z-index: 10;
    @include respond-to("max_mobile") {
			top: 20%;
		}

  }
}

.parallax-window {
  min-height: 400px;
  background: transparent;
}