.whatsapp {
	$circle-size: 65px;
	$circle-size-small: 55px;
	//
	display: inline-flex;
	justify-content: center;
	align-items: center;
  position: fixed;
  bottom: 70px;
	right: 33px;
	background: #25d366;
	border: 5px solid #fff;
  @include size($circle-size);
	border-radius: 100%;
	box-shadow: 0 0 5px rgba(black, .15);
  text-align: center;
  color: #fff;
  z-index: 999;
	transition: .3s;
	@include respond-to("max_tablet") {
		right: 12px;
		@include size($circle-size-small);
	}
	&::before, &::after {
		content: "";
		position: absolute;
		background: rgba(#25d366, .1);
		border-radius: 100%;
		animation: pulsate 1.5s ease-out;
		animation-iteration-count: infinite;
		opacity: 0;
	}
	&::before {
		@include size($circle-size + 15px);
		@include respond-to("max_tablet") {
			@include size($circle-size-small + 10px);
		}
	}
	&::after {
		@include size($circle-size + 35px);
		@include respond-to("max_tablet") {
			@include size($circle-size-small + 20px);
		}
	}
	.icon {
		font-size: 28px;
		@include respond-to("max_tablet") {
			font-size: 22px;
		}
	}
	&.is-active {
		transform: translateY(-70px);
		@include respond-to("max_tablet") {
			transform: translateY(-60px);
		}
	}
}