.contact{
  margin-bottom: 50px;
  &-map{
    width: 100%;
    height: 470px;
    margin-bottom: 50px;
    iframe{
      width: 100%;
      height: 100%;
    }
  }
  &-iletisim{
    width: 100%;
  }
  &-iletisim__box{
    display: flex;
    .contact-form{
      padding-left: 50px;
      padding-top: 30px;
      padding-right: 20px;
      height: 650px;
      background-color: white;
      clip-path: polygon(8% 0, 100% 0, 100% 60%, 100% 100%, 0 100%, 0% 60%, 0 10%);
      .title{
        font-size: 30px;
        font-weight: 600;
      }
      .text{
        padding-top: 20px;
        margin-bottom: 20px;
        font-size: 16px;
        color: grey;
      }
      form{
        
        ul{
          li{
            .button{
              margin-top: 20px !important;
              width: 100% !important;
            }
          }
        }
        input{
          height: 50px;
          background-color: #f6f6f6;
          color: #768492;
          padding-left: 10px;
          width: 100%;
        }
        textarea{
          margin-top: 30px;
          height: 280px;
          background-color: #f6f6f6;
          color: #768492;
          padding-left: 10px;
          padding-top: 10px;
          width: 100%;
        }
      }
    }
    .contact-local{
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
      margin-left: 30px;
      .local-box{
        margin-top: 50px;
      }
      .baslik{
       font-size: 30px;
       font-weight: 600;
       margin-bottom: -30px;
      }
      .title{
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 10px;
        display: flex;
        
      }
      .text{
        display: flex;
        align-items: center;
        h3{
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
  }
}
