.owl-theme {
	// Styling Next and Prev buttons
	.owl-nav {
		margin-top: 10px;
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		[class*='owl-'] {
			color: $nav-color;
			font-size: $nav-font-size;
			margin: $nav-margin;
			padding: $nav-padding;
			background: $nav-background;
			display: inline-block;
			cursor: pointer;
			border-radius: 3px;

			// &:hover {
			// 	background: $nav-background-hover;
			// 	color: $nav-color-hover;
			// 	text-decoration: none;
			// }
		}
		.disabled {
			opacity: $nav-disabled-opacity;
			cursor: default;
		}
	}

	// Styling dots
	.owl-nav.disabled + .owl-dots {
		margin-top: 50px;
	}

	.owl-dots {
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		.owl-dot {
			display: inline-block;
			outline: 0;
			zoom: 1;
			*display: inline;

			span {
				width: $dot-width;
				height: $dot-height;
				margin: $dot-margin;
				background: $dot-background;
				display: block;
				transition: .3s;
				-webkit-backface-visibility: visible;
				border-radius: $dot-rounded;
			}

			&.active,
			&:hover {
				span {
					background: $dot-background-active;
				}
			}

			&.active {
				span {
					width: 50px;
				}
			}
		}
	}
}

// -------------------------------------
//   Owl nav style
// ------------------------------------- 
.owl-nav-style {
	.owl-theme {
		.owl-nav {
			[class*="owl-"] {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				@include size(50px);
				font-size: 18px;
				background: rgba($color-text, .4);
				border: 1px solid transparent;
				border-radius: 50%;
				outline: 0;
				transition: .3s;
				&:hover {
					background: $color-text;
				}
			}
		}
	}

	&:not([class*="ui-pos-"]) {
		.owl-theme {
			.owl-nav {
				position: absolute;
				@include pos-top(50%, 0, 0);
				margin-top: -80px;
				
				[class*="owl-"] {
					position: absolute;
				}

				button {
					&.owl-prev {
						left: -70px;
					}
					&.owl-next {
						right: -70px;
					}
				}
			}
		}

		.owl-road {
			.owl-nav {
				position: absolute;
				@include pos-top(50%, 0, 0);
				margin-top: -80px;
				
				[class*="owl-"] {
					position: absolute;
				}
				
				
				button {
					@include respond-to("max_tablet") {
						top: 50px;
					}
					&.owl-prev {
						left: -70px;
						@include respond-to("max_tablet") {
							display: block;
							left: 0px;
							align-items: center;
							display: flex;
							justify-content: center;
							background-color: transparent;
						}
					}
					&.owl-next {
						right: -70px;
						@include respond-to("max_tablet") {
							display: block;
							right: 0px;
							align-items: center;
							display: flex;
							justify-content: center;
							background-color: transparent;
						}
					}
				}
			}
		}

		.owl-default {
			.owl-nav {
				position: absolute;
				@include pos-top(50%, 0, 0);
				margin-top: -20px;
				
				[class*="owl-"] {
					position: absolute;
					background-color: transparent !important;
					svg {
						path {
							stroke: #000 !important;
						}
					}
				}

				button {
					&.owl-prev {
						left: -70px;
					}
					&.owl-next {
						right: -70px;
					}
				}
			}
		}
	}
	
	// -------------------------------------
	//   position bottom right
	// ------------------------------------- 
	&.ui-pos-bottom-right {
		.owl-theme {
			.owl-nav {
				@include pos-bottom-right;
				position: relative;

				[class*="owl-"] {
					position: relative;
				}

				button {}
			}
		}
	}	

	// -------------------------------------
	//   position bottom left
	// ------------------------------------- 
	&.ui-pos-bottom-left {
		.owl-theme {
			.owl-nav {
				@include pos-bottom-left;
				position: relative;

				[class*="owl-"] {
					position: relative;
				}

				button {}
			}
		}
	}	

	// -------------------------------------
	//   position bottom center
	// ------------------------------------- 
	&.ui-pos-bottom-center {
		.owl-theme {
			.owl-nav {
				position: relative;
				text-align: center;

				[class*="owl-"] {
					position: relative;
				}

				button {}
			}
		}
	}

	// -------------------------------------
	//   position top right
	// ------------------------------------- 
	&.ui-pos-top-right {
		.owl-theme {
			.owl-nav {
				@include pos-top-right;

				[class*="owl-"] {
					position: relative;
				}

				button {}
			}
		}
	}
}