.blog{

  &-haber{
    margin-top: 30px;
    margin-bottom: 30px;
  }
  &-haber__body{
    .baslik{
      font-size: 30px;
      font-weight: 600;
      margin-top: 10px;
    }
    .text{
      font-weight: 400;
      color: #768492;
      margin-block: 20px;
    }
    .link{
      color: #ff8f26;
      transition: .6s;
      font-weight: 600;
      font-size: 18px;
      &:hover{
        color: darkorange;
      }
      i{
        margin-left: 10px;
      }
    }
  } 
}