// -------------------------------------
//   Position Top
// ------------------------------------- 
@mixin pos-top( $top:0, $right:0, $left:0 ){
  top: $top;
  right: $right;
  left: $left;
};
@mixin pos-top-right( $top: 0, $right: 0 ) {
  top: $top;
  right: $right;
}
@mixin pos-top-left( $top: 0, $left: 0 ) {
  top: $top;
  left: $left;
}

// -------------------------------------
//   Position Bottom
// ------------------------------------- 
@mixin pos-bottom( $right:0, $bottom:0, $left:0 ) {
  right: $right;
  bottom: $bottom;
  left: $left;
};
@mixin pos-bottom-right( $right:0, $bottom:0 ) {
  right: $right;
  bottom: $bottom;
};
@mixin pos-bottom-left( $bottom:0, $left:0 ) {
  bottom: $bottom;
  left: $left;
};

// -------------------------------------
//   Position ( Right, Left )
// ------------------------------------- 
@mixin pos-right-fixed( $top: 0, $right: 0, $bottom: 0 ) {
  top: $top;
  right: $right;
  bottom: $bottom;
}
@mixin pos-left-fixed( $top: 0, $bottom: 0, $left: 0 ) {
  top: $top;
  bottom: $bottom;
  left: $left;
}

// -------------------------------------
//   Position Fixed
// -------------------------------------  
@mixin pos-fixed( $value: 0 ) {
  left: $value;
  right: $value;
  top: $value;
  bottom: $value;
};

// -------------------------------------
//   Gutter Vertical
// -------------------------------------  
@mixin margin-v( $val: 0 ) {
  margin-top: $val;
  margin-bottom: $val;
};
@mixin padding-v ( $val: 0 ) {
  padding-top: $val;
  padding-bottom: $val;
};

// -------------------------------------
//   Gutter Horizontal
// ------------------------------------- 
@mixin margin-h ( $val: 0 ) {
  margin-left: $val;
  margin-right: $val;
};
@mixin padding-h ( $val: 0 ) {
  padding-left: $val;
  padding-right: $val;
};

// -------------------------------------
//   Size
// ------------------------------------- 
@mixin size ( $val: 0 ) {
  width: $val;
  height: $val;
}

// -------------------------------------
//   Input placeholder
// ------------------------------------- 
@mixin input-placeholder($color) {
  &::-webkit-input-placeholder { /* Edge */
    color: $color;
  }
  
  &:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: $color;
  }
  
  &::placeholder {
    color: $color;
  }
}

// -------------------------------------
//   Field
// ------------------------------------- 
@mixin field( $color, $gutter-l: 25px, $gutter-r: 25px, $height: $field-height ) {
  width: 100%;
  height: $height;
  border: 2px solid rgba($color-text, .1);
  border-radius: $border-radius-md;
  font-family: inherit;
  padding-left: $gutter-l;
  padding-right: $gutter-r;
  color: $color;
  font-size: 16px;
  font-weight: 500;
  background: #fff;
  transition: .3s;
  
  &:focus {
    outline: 0;
    border: 2px solid $color-primary;
  }

  @include respond-to("max_mobile") {
    height: 55px;
    padding-right: 15px;
    font-size: 14px;
  }

  @include input-placeholder( $color );
}

@mixin field-arrow {
  position: absolute;
  pointer-events: none;
  @include pos-top-right(50%, 25px);
  transform: translateY(-50%);
  font-size: 14px;
  opacity: .5;
  transition: .3s;
}

// -------------------------------------
//   Triangle top
// -------------------------------------
@mixin triangle-top($size, $color) {
  border-left: $size solid transparent;
  border-right: $size solid transparent;
  border-bottom: $size solid $color;
}

// -------------------------------------
//   Triangle bottom
// -------------------------------------
@mixin triangle-bottom($size, $color) {
  border-left: $size solid transparent;
  border-right: $size solid transparent;
  border-top: $size solid $color;
}