.project{
  margin-top: 100px;
  margin-bottom: 100px;
  .small-title{
    margin-bottom: 0px !important;
  }
  &-baslik{
    padding-top: 70px;
  }
  &-card{
    &:hover{
      .project-picture{
        img{
          transform: scale(1.1);
        }
      }
    }
  }
  &-body{

  }
  &-picture{
    overflow: hidden;
    img{
      transition: .6s;
    }
  }
  &-box{
    .min-text{
      color: #ff8f26;
      font-size: .55556rem;
      text-transform: uppercase;
      letter-spacing: .2em;
      font-size: 12px;
      margin-top: 17px;
      font-weight: 700;
    }
    .title{
      font-size: 1.33333rem;
      line-height: 1.5;
      margin-bottom: 0;
      font-weight: 600;
      padding-top: 6px;
    }
  }
}